import React, { useState, useContext, useEffect } from 'react';
import { UserContext } from '../UserContext';
import { useNavigate } from 'react-router-dom';
import { db } from '../firebase';
import { collection, getDocs, doc, setDoc } from 'firebase/firestore';

const ProfileForm = () => {
    const { user, setUser } = useContext(UserContext);
    const navigate = useNavigate();

    const [isEditing, setIsEditing] = useState(false);
    const [profileData, setProfileData] = useState(null); // null =   
    const [drawResult, setDrawResult] = useState(null);
    const [loading, setLoading] = useState(true);
    const placeholderImg = 'https://cdn-icons-png.flaticon.com/512/149/149071.png';

    useEffect(() => {
        if (!user?.id) return; //      id  login

        const loadData = async () => {
            setLoading(true);
            try {
                //   
                const peopleSnap = await getDocs(collection(db, "people"));
                const peopleList = peopleSnap.docs.map(doc => ({ id: doc.id, ...doc.data() }));

                //   
                const userData = peopleList.find(p => p.id === user.id);
                if (!userData) throw new Error("   !");

                setProfileData({
                    ...userData,
                    preferredGift: userData.preferredGift || '',
                    hobbies: userData.hobbies || '',
                    interests: userData.interests || '',
                    photoUrl: userData.photoUrl || placeholderImg
                });

                //   
                const coupledSnap = await getDocs(collection(db, "coupled"));
                const coupledList = coupledSnap.docs.map(doc => ({ id: doc.id, ...doc.data() }));

                //    user    
                const match = coupledList.find(c => c.giverId === user.id);

                if (match) {
                    const receiverFull = peopleList.find(p => p.id === match.receiverId);
                    setDrawResult({
                        ...receiverFull,
                        preferredGift: receiverFull?.preferredGift || '',
                        hobbies: receiverFull?.hobbies || '',
                        interests: receiverFull?.interests || ''
                    });
                } else {
                    setDrawResult(null);
                }

            } catch (err) {
                console.error("    :", err);
            } finally {
                setLoading(false);
            }
        };

        loadData();
    }, [user?.id]);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setProfileData(prev => ({ ...prev, [name]: value }));
    };

    const handleSave = async () => {
        if (!profileData?.id) return alert(',  .');

        try {
            setLoading(true);
            const userRef = doc(db, "people", profileData.id);
            await setDoc(userRef, profileData, { merge: true });

            setUser(profileData); //  UserContext
            setIsEditing(false);
        } catch (err) {
            console.error(err);
            alert('   !');
        } finally {
            setLoading(false);
        }
    };

    const handleLogout = () => {
        setUser(null);
        localStorage.removeItem('user');
        navigate('/login');
    };

    if (!user) return <h3> ,    !</h3>;
    if (loading || !profileData) return <p>   ...</p>;

    return (
        <div className="profile-form-container">
            <div className="profile-header">
                <button className="logout-button" onClick={handleLogout}> </button>
            </div>
            <div className='pair'>
                <section className="profile-section">
                    <h3> </h3>
                    <div className='result-card'>

                        <img
                            src={profileData.photoUrl}
                            alt={profileData.name}
                            className="profile-photo"
                        />
                        <h2>{profileData.name}</h2>
                        <p>{profileData.email}</p>

                        {!isEditing ? (
                            <div>
                                <p><strong> :</strong> {profileData.preferredGift || ''}</p>
                                <p><strong>:</strong> {profileData.hobbies || ''}</p>
                                <p><strong>:</strong> {profileData.interests || ''}</p>
                                <button onClick={() => setIsEditing(true)}> </button>
                            </div>
                        ) : (
                            <div className="profile-edit">
                                <input
                                    name="preferredGift"
                                    value={profileData.preferredGift}
                                    onChange={handleChange}
                                    placeholder="..."
                                />
                                <textarea
                                    name="hobbies"
                                    value={profileData.hobbies}
                                    onChange={handleChange}
                                    placeholder="..."
                                />
                                <textarea
                                    name="interests"
                                    value={profileData.interests}
                                    onChange={handleChange}
                                    placeholder="..."
                                />
                                <button onClick={handleSave}> </button>
                                <button onClick={() => setIsEditing(false)}> </button>
                            </div>
                        )}
                    </div>
                </section>
                <div className="gift-container">
                    <img src="/Gift_Box_in_Red_PNG_Clipart-276.png" alt="Gift" className="gift-img" />
                </div>
                <section className="draw-section">
                    <h3>   </h3>
                    {drawResult ? (
                        <div className="result-card">
                            <img
                                src={drawResult.photoUrl}
                                alt={drawResult.name}
                                className="profile-photo"
                            />
                            <h2>{drawResult.name}</h2>
                            <p>{drawResult.email}</p>
                            <div>
                                <p><strong> :</strong> {drawResult.preferredGift || ' '}</p>
                                <p><strong>:</strong> {drawResult.interests || ' '}</p>
                                <p><strong>:</strong> {drawResult.hobbies || ' '}</p>
                            </div>
                        </div>
                    ) : (
                        <button onClick={() => navigate('/random')}>   </button>
                    )}
                </section>
            </div>
        </div>
    );
};

export default ProfileForm;
